export const FlowContext = React.createContext({ q: "" });

const Flow = () => {
  const [reactFlowInstance, setReactFlowInstance] = useState({});

  const onLoad = (_reactFlowInstance) =>
    setReactFlowInstance(_reactFlowInstance);

  const nodeTypes = {
    theme: ThemeNode,
    group: GroupReferenceNode,
    meta: MetaEntityNode,
  };

  return (
    <div
      style={{
        height: "calc(100vh - 70px)",
        width: "100%",
        display: "flex",
      }}
    >
      <ReactFlow
        elements={data}
        onLoad={onLoad}
        snapToGrid={true}
        snapGrid={[15, 15]}
        nodeTypes={nodeTypes}
        // edgeTypes={edgeTypes}
        panOnScroll={true}
        minZoom={0.1}
      >
        <Controls />
        <Background color={"transparent"} size={0.8} gap={16} />
      </ReactFlow>
    </div>
  );
};

export default Flow;
