import { useContext, useEffect, useRef, useState } from "react";
import styled, { ThemeContext } from "styled-components";
import { useStoreState } from "react-flow-renderer";

import MetaLink from "../Icons/MetaLink";
import VerticalDots from "../Icons/VerticalDots";
import ChevronIcon from "../Icons/ChevronIcon";
import MakeGroupIcon from "../Icons/MakeGroupIcon";

const PopupNav = styled.ul`
  border-radius: 3px;
  background-color: #fff;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
    rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
  position: absolute;
  top: 2.4rem;
  right: 0;
  z-index: 10;
`;

const PopupNavItem = styled.li`
  padding: 0.75rem 1.5rem 0.75rem 0.25rem;
  display: flex;
  flex-direction: row;
  align-items: center;

  span {
    font-weight: normal;
    font-size: 1rem;
  }

  svg {
    visibility: hidden;
  }

  &:hover {
    cursor: pointer;
    svg {
      visibility: visible;
    }
  }
`;

const ButtonContainer = styled.button`
  border: 0;
  background: none;
  margin: 0;
  padding: 0;

  &:hover {
    cursor: pointer;
    transform: scale(0.9, 0.9);
  }
`;

const FlexRow = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
`;

const EditableNodePopupNav = styled(PopupNav)`
  &:before {
    transform-origin: 0 0;
  }
`;

const EditableNodePopupNavItem = styled(PopupNavItem)`
  padding: 0.1rem 0.25rem;
  max-width: 5rem;
`;

const EditableNode = ({ data, type }) => {
  const theme = useContext(ThemeContext);

  const [menuButtonPositions, setMenuButtonPositions] = useState({
    x: 0,
    y: 0,
  });

  const [showMenu, setShowMenu] = useState(false);
  const [editLabel, setEditLabel] = useState(false);
  const nodes = useStoreState((store) => store.nodes);
  const nodeRef = useRef(null);

  useEffect(() => {
    const onClickOutside = (e) => {
      if (nodeRef.current && !nodeRef.current.contains(e.target)) {
        setShowMenu(false);
      }

      if (nodeRef.current && !nodeRef.current.contains(e.target)) {
        setShowMenu(false);
      }
    };

    document.addEventListener("mousedown", onClickOutside);

    return () => {
      document.removeEventListener("mousedown", onClickOutside);
    };
  }, []);

  const renderContent = (type) => {
    if (type === "theme") {
      return (
        <>
          <MakeGroupIcon size={18} color={"#18a6b1"} />
          <span>( Theme )</span>
        </>
      );
    }

    return (
      <>
        <MetaLink size={18} color={"#6cbd54"} />
        <span>( Group )</span>
      </>
    );
  };

  return (
    <FlexRow
      onDoubleClick={() => {
        setEditLabel(true);
      }}
    >
      <>
        {renderContent(type)}
        {data.label === `${type} node` ? "unlabeled" : data.label}
      </>
      <ButtonContainer
        onClick={(event) => {
          setShowMenu(!showMenu);
        }}
        style={{ marginLeft: "1rem" }}
      >
        <VerticalDots color={"#696969"} size={14} />
      </ButtonContainer>
      {showMenu ? (
        <EditableNodePopupNav ref={nodeRef}>
          <EditableNodePopupNavItem
            onClick={() => {
              navigator.clipboard.writeText(data.label);
              setShowMenu(false);
            }}
          >
            <ChevronIcon size={24} color={"#18a6b1"} />
            <span>Copy text</span>
          </EditableNodePopupNavItem>
        </EditableNodePopupNav>
      ) : null}
    </FlexRow>
  );
};
export default EditableNode;
