import { FC, memo, useContext } from "react";
import styled, { ThemeContext } from "styled-components";
import {
  Handle,
  Position,
  NodeProps,
  Connection,
  Edge,
} from "react-flow-renderer";

import { ArabicLabel, FlowNodeLabel, SecondaryNodeContainer } from ".";
import { FlowContext } from "../FlowPage";

const onConnect = (params) => console.log("handle onConnect", params);

const isValidConnection = (connection) => {
  console.log(connection);
  return connection.source?.includes("theme") || false;
};

const MarkerHighlight = styled(ArabicLabel)`
  position: relative;
  background: url("/images/brushstroke.png");
  background-repeat: no-repeat;
  background-size: 100% 75%;
  background-position-y: center;
`;

const MetaEntityNode = ({ data, selected, isConnectable }) => {
  const theme = useContext(ThemeContext);
  const flowState = useContext(FlowContext);

  return (
    <SecondaryNodeContainer
      borderColor={selected ? "#008b96" : "#ad806d"}
      style={{ position: "relative" }}
    >
      <Handle
        type="source"
        position={Position.Top}
        onConnect={onConnect}
        isValidConnection={isValidConnection}
      />
      <FlowNodeLabel color={data.labelColor}>{data.labelType}</FlowNodeLabel>
      <ArabicLabel as="p">
        {flowState.q === data.label ? (
          <MarkerHighlight as="mark">{data.label}</MarkerHighlight>
        ) : (
          data.label
        )}
      </ArabicLabel>
    </SecondaryNodeContainer>
  );
};

export default memo(MetaEntityNode);
